import { _decorator, Component, JsonAsset, Node, resources, SpriteFrame, Prefab, instantiate } from 'cc';
const { ccclass, property } = _decorator;
import { CardData } from './CardData';
import { Card } from './Card';

@ccclass('DeckManager')
export class DeckManager extends Component {

    @property(Prefab)
    private cardPrefab: Prefab = null!;

    private deck: CardData[] = [];

    start() {
        let deckCount = this.deck.length;
        for (let i = 0; i < deckCount; i++) {
            const cardData = this.deck[i];
            const cardNode = instantiate(this.cardPrefab);
            cardNode.setPosition((i % 13) * 30, Math.floor(i / 13) * -40, 0);
            this.node.addChild(cardNode);
            let card = cardNode.addComponent(Card);
            card.init(cardData);
        }
    }

    onLoad() {
        resources.load('deck', JsonAsset, (err, asset) => {
            if (err) {
                console.error('Failed to load card data:', err);
                return;
            }
            this.deck = asset.json as CardData[];
            this.loadSprites();
        });
    }

    loadSprites() {
        for (const card of this.deck) {
            resources.load('sprites/Cards/'+card.sprite+'/spriteFrame', SpriteFrame, (err: any, spriteFrame) => {
                if (!err) {
                    card.spriteFrame = spriteFrame;
                } else {
                    console.error(`Failed to load sprite for ${card.rank} of ${card.suit}:`, err);
                }
            });
        }
    }

    update(deltaTime: number) {
        
    }
}

